@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Poppins:wght@200;400;500&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
  overflow: hidden;
}

body {
  font-family: "Poppins";
}

@media (max-height: 850px) {
  * {
    overflow-y: auto;
    overflow-x: hidden;
  }
}
